.home-container button{
    position:absolute;
    top: 47%;
    right: 20%;
    padding: 10px;
    font-size: 25px;
    transition: 0.2s;
    cursor: pointer;

    border: 2px solid crimson;
    border-radius: 3px;
}

.home-container button:hover{
    color:crimson;
    transform: translateX(7px);
}

.arrow{
    font-size: 24px;
    position: absolute;
    top: 12px;
}

@media only screen and (max-width: 1100px) {
    .home-container button{
        right: 10%;
    }
  } 

  @media only screen and (max-width: 800px) {
    .home-container button{
        right: 25%;
    }
  } 